import React from "react"
import MeOnRock from "../Images/me_on_rock.png"

function AboutPage(){
    return(
        <div id="about">
            <h1 id="TitleText">About</h1>
            <p id="myinfo">
                Michael Orlando: a PC enthusiast  that loves to solve 
                problems with code and has an aptitude for security. Always learning
                new frameworks tools, and security trends.
            </p>
            <h3>Prefered Programing Languages</h3>
            <ul>
                    <li>C++</li>
                    <li>Python</li>
                    <li>Ruby</li>
                    <li>javascript,HTML,CSS</li>
            </ul>
            
            <img id="MeOnRock" src={MeOnRock} />
        </div>
    )
}

export default AboutPage

