import React from "react"
import '../CSS/NavFullScreen.css'


function NavFullScreen(){
    function MenuSelected() {
        var top = document.getElementById("top");
        var middle = document.getElementById("middle");
        var bottom = document.getElementById("bottom");
        var overlayNav = document.getElementById("overlayNav");
        var fpadding = document.getElementById("fpadding");

        top.classList.toggle("active");
        middle.classList.toggle("active");
        bottom.classList.toggle("active");
        overlayNav.classList.toggle("active");
        fpadding.classList.toggle("active");
    }

    // should remove componets and add them conditionaly
    // but react shouldent even be used for this site... 
    // this would be too overcomplicated for a simple menu the react way
    function HomePage(){
        MenuSelected()
        var About = document.getElementById("about");
        var Home = document.getElementById("fpadding");
        Home.style.display = "block";
        About.style.display = "none";
    }
    function AboutPage(Home){
        MenuSelected()
        var About = document.getElementById("about");
        var Home = document.getElementById("fpadding");
        Home.style.display = "none";
        About.style.display = "block";
    }
    function BlogPage(){
        MenuSelected()
    }
    
    return(
        <div className="content-container">
            <div onClick={MenuSelected} id="menu-icon-shape">
                <div id="menu-icon">
                    <div id="top"></div>
                    <div id="middle"></div>
                    <div id="bottom"></div>
                </div>
            </div>

            <div id="overlayNav">
                <div id="nav-content">
                    <ul>
                        <li><a onClick={HomePage} href="#">Home</a></li>
                        <li><a onClick={BlogPage} href="#">Blog</a></li>
                        <li><a onClick={AboutPage} href="#">About</a></li>
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default NavFullScreen